import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, CityApi, Contractor_specialization_masterApi } from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-specialization.component.html'
})
export class EditSpecializationComponent {
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	specializationName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	specializationStatus:boolean = false;
	bookImage:any;
	loginFlagToken:any;
	contractorCount : number = 0;
	municipalityCount : number = 0;
	equipmentId : number = 0;
	

	editSpecialization={
		"specialization_id":0,
		"specialization_name":"",
		"status": 0,
		//"updated_date":0
	}
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService, private cityApi:CityApi,private specializationApi:Contractor_specialization_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"specialization_id":this.id
		}
		this.specializationApi.getSpecialization(dataArrObj).subscribe(data=>{
			
			if(data){
				let result = data.result[0];
				this.equipmentId = result.specialization_id;
				this.specializationName = result.specialization_name;
				this.specializationStatus = (result.status)?true:false;
				this.contractorCount = result.usedCount;
				console.log("contractorCount ",this.contractorCount);
				this.editSpecialization={
					"specialization_id":result.specialization_id,
					"specialization_name": result.specialization_name,
					"status": result.status,
					//"updated_date":result.updated_date
				}


			}
			this.loading = false;
		},
		err=>{
			console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["specialization"]);
	}

	async submit(specializationForm) {
		this.submitted = true;
		if (specializationForm.valid) {
			this.loading = true;

			let date = new Date();
			let currentdate = date.getTime();

			this.editSpecialization.specialization_id = this.equipmentId;
			this.editSpecialization.specialization_name = this.capital_letter(this.specializationName);
			this.editSpecialization.status = (this.specializationStatus)?1:0;
			//this.editSpecialization.updated_date = currentdate;

			

			//updating equipments-master
		
			this.ifNameExist(this.editSpecialization).then((total)=>{
				if(total > 0){
					this.loading = false;
					this.result = "Specialization name already exist";
					this.display = true;
				}else{
					console.log("this.editSpecialization ",this.editSpecialization);
					this.specializationApi.upsertPatch(this.editSpecialization).subscribe((data)=>{
						this.loading = false;
						this.result = "success";
						this.display = true;
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
				}
			},(err)=>{
				this.loading = false;
				this.result = "Something went wrong";
				this.display = true;
			});

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{

				this.specializationApi.getSpecializationCountName({ "specialization_name": dataObj.specialization_name,"status":dataObj.status}).subscribe((data)=>{
					resolve(data.result[0].total);
		
					},err=>{
						reject(err);
				});
			
			}catch(err){
				reject(err);
			}
		});
		
		
	}

	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}

	
}


