import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { EditSpecializationComponent } from './edit-specialization.component';
import { EditSpecializationRoutingModule} from './edit-specialization-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    EditSpecializationRoutingModule,
    DialogModule,
    EditorModule,
	  BusyModule
  ],
  declarations: [
    EditSpecializationComponent
  ]
})
export class EditSpecializationModule { }



